import React, { useState } from "react";
import { Formik, Form, Field } from "formik";
import { TextField } from "formik-mui";
import * as Yup from "yup";
import TelephoneField from "./TelephoneField";
import axios from "axios";
import {
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Snackbar,
  Alert,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import MiniMapComponent from "./MiniMapComponent/MiniMapComponent";
import "./AddNewAnnouncement.css";

const AnnouncementSchema = Yup.object().shape({
  announcement: Yup.string().min(3).required("  "),
  description: Yup.string().min(3).required("  "),
  price: Yup.number().min(3).required("     "),
  firstName: Yup.string().min(3).required("  '"),
  lastName: Yup.string().min(3).required("  "),
  mobile: Yup.string()
    .matches(
      /^(\+38)?\s?\(?\d{3}\)?\s?\d{3}(-|\s)?\d{2}(-|\s)?\d{2}$/,
      "  "
    )
    .required("   "),
  email: Yup.string()
    .email(" e-mail")
    .required("  e-mail"),
  imgUrl: Yup.string().min(3),
});

const AddNewAnnouncement = () => {
  const [coordinates, setCoordinates] = useState({ lat: 0, lng: 0 });
  const [age, setAge] = React.useState("");
  const [open, setOpen] = React.useState(false);
  const navigate = useNavigate();
  const handleClick = () => {
    setOpen(true);
    setTimeout(() => {
      navigate("/");
    }, 2500);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleChange = (e) => {
    setAge(e.target.value);
  };
  return (
    <div>
      <Formik
        initialValues={{
          category: " ",
          announcement: "",
          description: "",
          price: "",
          firstName: "",
          lastName: "",
          mobile: "",
          email: "",
          imgUrl: "",
          objectLocation: "",
        }}
        validationSchema={AnnouncementSchema}
        onSubmit={(values, { setSubmitting }) => {
          const fullData = { ...values, coordinates };
          axios
            .post("http://localhost:3001/save-data", fullData)
            .then((response) => {
              setSubmitting(false);
            })
            .catch((error) => {
              console.error("There was an error saving the data:", error);
              setSubmitting(false);
            });
        }}
      >
        {({ isSubmitting, errors, touched }) => (
          <Form className="form__container">
            <div className="form">
              <FormControl fullWidth>
                <InputLabel id="demo-simple-select-label">
                   *
                </InputLabel>
                <Select
                  type="object"
                  label=" "
                  className="select"
                  onChange={handleChange}
                  value={age}
                >
                  <MenuItem value={10}> </MenuItem>
                </Select>
              </FormControl>
              <Field
                fullWidth
                component={TextField}
                name="announcement"
                type="announcement"
                label="   *"
                className="input"
              />

              <Field
                fullWidth
                component={TextField}
                name="description"
                type="description"
                label="   *"
                className="description"
              />

              <Field
                fullWidth
                component={TextField}
                name="price"
                type="price"
                label="   *"
                className="input"
              />

              <Field
                fullWidth
                component={TextField}
                name="firstName"
                type="text"
                label=" ' *"
                className="input"
              />

              <Field
                fullWidth
                component={TextField}
                name="lastName"
                type="text"
                label="  *"
                className="input"
              />

              <TelephoneField variant="outlined" className="input" />

              <Field
                fullWidth
                component={TextField}
                name="email"
                type="email"
                label="  email *"
                className="input"
              />

              <Field
                fullWidth
                component={TextField}
                name="imgUrl"
                type="imgUrl"
                label="    *"
                className="input"
              />

              <MiniMapComponent onCoordinatesChange={setCoordinates} />

              <div className="buttons__wrapper">
                <a
                  href="/"
                  className="miniMap__btn"
                  style={{
                    backgroundColor: "#fff",
                    color: "#283149",
                    textDecoration: "none",
                  }}
                >
                  
                </a>
                <button
                  onClick={handleClick}
                  className="miniMap__btn"
                  type="submit"
                  disabled={isSubmitting}
                  style={{
                    textDecoration: "none",
                  }}
                >
                  
                </button>
                <Snackbar
                  open={open}
                  autoHideDuration={6000}
                  onClose={handleClose}
                >
                  <Alert
                    onClose={handleClose}
                    severity="success"
                    variant="filled"
                    sx={{ width: "100%" }}
                  >
                     !
                  </Alert>
                </Snackbar>
              </div>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default AddNewAnnouncement;
